import React, { useState, useEffect, useCallback } from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";
import { DataGrid, GridToolbar } from "@mui/x-data-grid";
import style from "./style.css";
import { Button } from "@mui/material";
import Box from "@mui/material/Box";
import DialogContent from "@mui/material/DialogContent";
import CircularProgress from "@mui/material/CircularProgress";
import Dialog from "@mui/material/Dialog";

function PendingCommissioningTaskBody(props) {
  let [items, setItems] = useState([]);
  let [openDialog, setOpenDialog] = useState(false);
  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });

  //Sample code taken temp from another page //

  useEffect(() => {
    setOpenDialog(true);
    axiosInstance
      .get("projonline/gettasks/toSubmitCommission", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        // setItems(res.data.tasks.filter((obj) => (obj["taskStatus"] !== 'Task handed over')))   // Test codes
        // setItems(res.data.tasks.filter((obj) => (obj["taskStatus"] !== 'Subcon allocated')))
        // setItems(res.data.tasks.filter((obj) => (obj["taskStatus"] !== 'Task rejected')))
        // setItems(res.data.tasks.filter((obj) => (obj["taskStatus"] !== 'Task accepted')))
        // setItems(res.data.tasks.filter((obj) => (obj["taskStatus"] !== 'Task commenced')))
        // setItems(res.data.tasks.filter((obj) => (obj["taskStatus"] !== 'Withdraw')))

        setItems(
          res.data.tasks.filter(
            (obj) =>
              // obj["taskStatus"] !== 'Task handed over' &&
              // obj["taskStatus"] !== 'Subcon allocated' &&
              // obj["taskStatus"] !== 'Task rejected' &&
              // obj["taskStatus"] !== 'Task accepted' &&
              // obj["taskStatus"] !== 'Task commenced' &&
              // obj["taskStatus"] !== 'Withdraw' &&
              obj["comStatus"] !== "Site Commissioned"
          )
        );

        setOpenDialog(false);
        // console.log(res.data.tasks)
        return;
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  //Below Code on Test //

  const handleClick = (event, cellValues) => {
    // console.log(cellValues)
    props.history.push({
      pathname: "/pendingCommissioningSpecificTaskBody",
      data: cellValues,
    });
  };

  //Code on Test //

  const columns = [
    { field: "taskRef", headerName: "Task ID", width: 350 },
    { field: "siteID", headerName: "Site ID", width: 130 },
    {
      field: "companyName",
      headerName: "Assigned Subcon / Vender",
      width: 220,
      valueGetter: (params) => params.row.assignedSubcon.companyName,
      width: 350,
    },
    {
      field: "assignedMobitelOfficer",
      headerName: "Assinged Officer",
      width: 270,
      valueGetter: (params) => params.row.assignedMobitelOfficer.name,
    },
    // { field: 'taskRaisedDate', headerName: 'Initiated date', width:180,  valueGetter: (params) => params.row.taskHistory.find(x=>x.taskStatus==='Subcon allocated').performedDate.split('T')[0]},
    {
      field: "To Submit Commission",
      width: 270,
      renderCell: (cellValues) => {
        return (
          <Button
            variant="contained"
            color="primary"
            onClick={(event) => {
              handleClick(event, cellValues);
            }}
          >
            To Submit Commission
          </Button>
        );
      },
    },
  ];

  return (
    <div>
      {/* Content Wrapper. Contains page content */}
      <div className="content-wrapper">
        <div className="content-header">
          <section className="content-header">
            <div className="container-fluid">
              <div className="row mb-2">
                <div className="col-sm-6">
                  <h1>All Tasks to Submit Commissioning</h1>
                </div>
              </div>
            </div>
            {/* /.container-fluid */}
          </section>
        </div>

        <div style={{ height: 610, width: "100%" }}>
          <DataGrid
            getRowId={(item) => item._id}
            rows={items}
            columns={columns}
            pageSize={15}
            rowsPerPageOptions={[15]}
            rowHeight={30}
            headerHeight={40}
            components={{ Toolbar: GridToolbar }}
          />
        </div>
      </div>
      <Dialog
        open={openDialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default withRouter(PendingCommissioningTaskBody);
